<template>
  <v-container>
    <v-row class="text-center pt-10">
      <v-col class="" cols="9" > 
        <v-text-field
            label="Insira um Nome"
            outlined
            v-model="campoInserir"            
            @keyup.enter="addNome"
          ></v-text-field>
          </v-col>
          <v-col cols="3"><v-btn color="warning" @click="addNome">Cadastrar</v-btn>
          </v-col>

          <v-col class="" cols="12" > <v-list
      flat
      subheader
    >
      <v-subheader>Pessoas</v-subheader>

      <v-list-item-group
        multiple
        active-class=""
      >
       <div v-for=" nome, index in $store.state.nomes" :key="index">
          <Nome 
          :nome="nome"
          />
       </div>
      </v-list-item-group>
    </v-list></v-col>
   </v-row>
  </v-container>
</template>

<script>
import Nome from '../components/nomes/NomeView.vue'

  export default {
    name: 'HomeView',
    components: {
      Nome
    },
    data () {
      return {
        campoInserir: null,
        nomes: [ ],
      }
    },
    methods: {
      addNome(){
        this.$store.commit('colocarPessoa', this.campoInserir)        
          this.campoInserir = null
        },
      
      removeNome(state, id){
       state.nomes = state.nomes.filter(nome => nome.id !== id)
      }    
    },
  }
</script>
