<template>
  <div>
      <v-dialog
      v-model="dialog"
      persistent
      max-width="290"
    >
      <v-card>
        <v-card-title class="text-h5">
          Editar 
        </v-card-title>
        <v-divider class="mb-4"></v-divider>
        <v-card-text>Insira o nome abaixo</v-card-text>
        <v-text-field
        class="px-3"
        label="Nome"
        outlined
        v-model="$store.state.nomes.pessoa"
        >
        </v-text-field>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="red darken-1"
            text
            @click="$emit('fechaModal')"
          >
            Cancelar
          </v-btn>
          <v-btn
            color="green darken-1"
            text
            @click="$store.commit('editarTarefa', {pessoa, id: nome.id})"
          >
            Salvar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  export default {
    props: ['nome'],
    data () {
      return {
        dialog: true,
        pessoa: null,
      }
    },
    created() {
        this.pessoa = this.nome.pessoa
    },
  }
</script>

